header {
  display: flex;
  justify-content: space-between;
  background-color: #313233;
  position: fixed;
  width: 100%;
  font-family: 'Nunito', sans-serif;
}

.headline {
  padding-left: 4rem;
  color: white;
  font-size: 1.8rem;
}

nav {
  display: flex;
}

nav ul {
  list-style: none;
  display: flex;
  align-items: center;
}

nav ul:focus {
  outline: none;
}

nav ul li:focus {
  outline: none;
}

nav ul a {
  color: white;
  opacity: .6;
}

nav ul li {
  margin-right: 2rem;
  transition: all ease .5s;
  font-weight: 700;
}

nav ul a:hover {
  opacity: 1;
}

nav ul li .active {
  color: white;
  opacity: 1;
}

@media (max-width: 800px) {
  header {
    flex-direction: column;
  }

  .headline {
    font-size: 1.5rem;
    padding-left: 0;
  }

  nav {
    justify-content: center;
  }
 }