@import url(https://fonts.googleapis.com/css?family=Merriweather:300,400&display=swap" rel="stylesheet");
@import url(https://fonts.googleapis.com/css?family=Nunito:300&display=swap" rel="stylesheet");

body {
  margin: 0;
  font-family: -apple-system, 'Merriweather', "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

a {
  text-decoration: none;
}

.news {
  border-bottom: solid .5px rgba(114, 145, 176, .9);
  padding: 1rem;
  text-align: left;
  display: grid;
  grid-template-columns: 20rem auto;
  grid-template-rows: auto;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.news h1 {
  font-family: 'Merriweather', serif;
  max-width: 45rem;
  align-self: flex-start;
  grid-column: 2;
  margin-top: 0;
  color: #36454f;
}

.news p {
  font-family: 'Nunito', sans-serif;
  font-weight: 300;
  grid-column: 2;
  margin-top: 0;
  line-height: 1.5;
}

.articles {
  display: flex;
  flex-direction: column;
  padding-top: 8rem;
  margin: auto;
  max-width: 70rem;
  padding-left: 4rem;
  padding-right: 4rem;
}

.news-image {
  grid-row: 1 / span 2;
  grid-column: 1;
  border-radius: .5rem;
  width: 17rem;
  height: 11rem;
  background-position: center;
  background-size: cover;
  box-shadow: -2px 2px 9px 0px rgba(76,76,76,0.65);
}

.button-all {
  background-color: #7291b0;
  border-color: transparent;
  border-radius: 25px;
  padding: .6rem .9rem;
  color: white;
  margin-top: 2rem;
  box-shadow: 0px 0px 8px 1px rgba(76,76,76,0.3);
  cursor: pointer;
  font-size: .8rem;
  border: solid 1.2px #7291b0;
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
  font-weight: 700;
  margin-bottom: 5rem;
}

.button-all:hover {
  background-color: transparent;
  border: solid 1.2px #4d6782;
  color: #4d6782;
}

.button-all:focus {
  outline: none;
}

@media (max-width: 800px) {
  .news {
    display: block;
  }

  .news h1 {
    margin-top: 2rem;
    max-width: 35rem;
  }

  .news-image {
    width: 100%;
  }

  .articles {
    padding-top: 12rem;
  }
 }

header {
  display: flex;
  justify-content: space-between;
  background-color: #313233;
  position: fixed;
  width: 100%;
  font-family: 'Nunito', sans-serif;
}

.headline {
  padding-left: 4rem;
  color: white;
  font-size: 1.8rem;
}

nav {
  display: flex;
}

nav ul {
  list-style: none;
  display: flex;
  align-items: center;
}

nav ul:focus {
  outline: none;
}

nav ul li:focus {
  outline: none;
}

nav ul a {
  color: white;
  opacity: .6;
}

nav ul li {
  margin-right: 2rem;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
  font-weight: 700;
}

nav ul a:hover {
  opacity: 1;
}

nav ul li .active {
  color: white;
  opacity: 1;
}

@media (max-width: 800px) {
  header {
    flex-direction: column;
  }

  .headline {
    font-size: 1.5rem;
    padding-left: 0;
  }

  nav {
    justify-content: center;
  }
 }
.weather {
  display: flex;
  align-items: center;
  padding-right: 4rem;
  color: white;
  height: 100%;
}

.icon {
  padding-left: .5rem;
}

.city {
  padding-left: .5rem;
  font-size: .9rem;
}

@media (max-width: 800px) {
  .city {
    display: none;
  }

  .weather {
    justify-content: center;
    padding-right: 0;
    padding-bottom: .5rem;
  }

  .weather h2 {
    font-size: 1rem;
  }
}

.quotes {
  text-align: center;
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
}

.quotes h3 {
  font-family: 'Merriweather', serif;
  font-style: italic;
  max-width: 50rem;
  color: #585a5c;
}

.author {
  display: flex;
  justify-content: flex-end;
  font-size: .8rem;
  color: #717478;
}

@media (max-width: 800px) {
  .quotes {
    margin: 0 1rem 5rem;
  }
}
